import React from 'react';
import './App.css';
import ToggleButton from '@material-ui/lab/ToggleButton';
import ToggleButtonGroup from '@material-ui/lab/ToggleButtonGroup';
import Tags from './tags';
import Recommend from './recommend';


export default class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      tab: "tags"
    };
  }
  handleTab(e) {
    this.setState({
      tab: e.value
    });
  }

  render() {
    let tagsTab = <Tags/>
    let recommendTab = <Recommend/>
    return (
      <div className="App">
        <img src={"/images/LogoFullB.png"} className="App-logo" alt="logo" />
        <ToggleButtonGroup
          exclusive
          onChange={this.handleTab}
          aria-label="tabs"
        >
          <ToggleButton value="tags" aria-label="left aligned">
              Tags
          </ToggleButton>
          <ToggleButton value="recommend" aria-label="right aligned">
              Recommend
          </ToggleButton>
        </ToggleButtonGroup>
        {(this.state.tab === "tags") ? {tagsTab} : {recommendTab} }
      </div>
    );
  }
}



